<template>
    <div class="content">
        <div id="select_contentTitleSearch">
        	<div class="Choose">
        		<span class="select_title">人员选择</span>
        		<!-- <router-link class="Down" to="/jituan"><i class="iconfont icon-arrow_right"></i></router-link> -->
        	</div>
        	<!-- <div class="Find">
        		<i class="iconfont icon-fangdajing"></i>
        	</div> -->
        </div>
        <div class="bigsearch">
            <div class="search">
              <input v-model="keyword" class="search-input" type="text" placeholder="搜索" />
              <button @click="getNameInfo()">确定</button>
            </div>
        </div>

        <!-- <div class="select_crumbs_list">
            <div class="select">
                <router-link to="/danwei">雅居乐集团控股公司</router-link>
                <span><i class="iconfont icon-arrow-left"></i></span>
                <router-link to="/danwei">本单位</router-link>
                <span><i class="iconfont icon-arrow-left"></i></span>
                <router-link to="/danwei">运营与信息管理部</router-link>
                <span><i class="iconfont icon-arrow-left"></i></span>
                <span>信息部</span>
        	</div>
        </div> -->
        <div class="scroller">
        	<ul id="select_listContent">
        		<li class="Recent" v-for="item of memberList" :key="item.id">
        			<div class="avatar">
        				<input class="radio_type" v-model="radvalue" type="checkbox" :value="item">
        				<img src="../../assets/img/Avatar.jpg" alt="">
        			</div>
        			<div class="information">
        				<span>{{item.name}}</span>
        				<h6>{{item.code}}</h6>
        			</div>
        		</li>

        	</ul>
        </div>
        <footer>
        	<button type="submit" @click="handlebtnclick()">确认</button>
        </footer>
    </div>
</template>

<script>
export default {
    name:'Common',
    data(){
        return{
            //本部门人员
            memberList:null,
            //所有人员
            list:null,
            //搜索框内容
            keyword:null,
            //选中人员的值
            radvalue:[]
        }
    },
    mounted() {
        this.initpage()
    },
    methods:{
        //根据名称获取人员信息
        getNameInfo(){
            var getname = encodeURI(this.keyword);
            var getname2 = this.keyword;
            console.log(getname);
            var token = this.getToken("rest", "123456");
            this.$axios.get('http://10.1.19.170/seeyon/rest/orgMembers/name/'+getname+'?token='+token)
                .then(res => {
                    this.memberList = res.data
                })
                .catch(err => {
                    console.log(err);
                });
            this.$axios.get('http://10.1.19.170/seeyon/rest/orgMember/code/'+getname2+'?token='+token)
                .then(res => {
                    this.memberList = res.data
                })
                .catch(err => {
                    console.log(err);
                });
        },
        //确认点击事件
        handlebtnclick(){
            console.log(this.radvalue);
            let nameObj = {};
            nameObj = this.radvalue;
            localStorage.setItem('Personnel',JSON.stringify(nameObj));
            // var list5 = JSON.stringify(localStorage.getItem("Personnel"));
            // console.log(list5)
            window.close()
        },
        initpage() {
             var token = this.getToken("rest", "123456");
             //根据loginname获取人员信息
             // var code = "A000543"
             // var member = this.getUserByCode(code,token)
             //console.log(member)
             //var orgAccountName = member[0].orgAccountName
             //console.log(orgAccountName)
             //获取部门下所有人员
             this.memberList = this.getUserListByDepartmentId("-2936870727963884205",token)
             console.log(this.memberList)
             //获取部门子部门
             //var subDepartment = new Array()
             //this.subDepartment=this.getSubDepartmentByParent("-2936870727963884205",token)
             //console.log(subDepartment)
             //获取部门父部门
             //var parentDepartment = this.getParentDepartmentByChild("-2936870727963884205",token)
             //console.log(parentDepartment)

		},
		getToken(username, password) {

            var url = "http://10.1.19.170";
            var token = null;

            $.ajax({
                url: (url + "/seeyon/rest/token/"+username+"/"+password),
                type: "get",
                dataType: "json",
                async: false,
                //data: {"userName":"' + username + '", "password":"' + password + '"},
                processData: false,
                headers: {"Accept": "application/json", "Content-Type": "application/json"},
                success: function (data) {
                    token = data.id;
                    console.log("getToken:", data)
                },
                error: function (XMLHttpRequest, textStatus, errorThrown) {
                    console.log("获取 TOKEN 失败！");
                }
            });


            return token;
        },
        getUserByCode(code,tk){
            var url = "http://10.1.19.170";
            var member = {}
            var header={
                "Accept": "application/json; charset=utf-8",
                "Accept-Language": "zh-CN",
                "Content-Type": "application/json; charset=utf-8"
                //"token": tk
            }

            $.ajax({
                url: url + "/seeyon/rest/orgMember/code/"+code+"?token="+tk,
                type: "get",
                dataType: "json",
                async: false,
                headers: header,
                processData: false,
                success: function (data) {
                    member = data
                },
                error: function (XMLHttpRequest, textStatus, errorThrown) {
                    console.log("获取 TOKEN 失败！");
                }
            });
            return member
        },
        getUserListByDepartmentId(departmentId,tk){
            var url = "http://10.1.19.170";
            var memberList = new Array()
            var header={
                "Accept": "application/json; charset=utf-8",
                "Accept-Language": "zh-CN",
                "Content-Type": "application/json; charset=utf-8"
                //"token": tk
            }

            $.ajax({
                url: url + "/seeyon/rest/orgMembers/department/"+departmentId+"?token="+tk,
                type: "get",
                dataType: "json",
                async: false,
                headers: header,
                processData: false,
                success: function (data) {
                    memberList = data
                },
                error: function (XMLHttpRequest, textStatus, errorThrown) {
                    console.log("获取 TOKEN 失败！");
                }
            });
            return memberList
        },
        getSubDepartmentByParent(departmentId,tk){
            var url = "http://10.1.19.170";
            var memberList = new Array()
            var header={
                "Accept": "application/json; charset=utf-8",
                "Accept-Language": "zh-CN",
                "Content-Type": "application/json; charset=utf-8"
                //"token": tk
            }

            $.ajax({
                url: url + "/seeyon/rest/orgDepartments/children/"+departmentId+"?token="+tk,
                type: "get",
                dataType: "json",
                async: false,
                headers: header,
                processData: false,
                success: function (data) {
                    memberList = data
                },
                error: function (XMLHttpRequest, textStatus, errorThrown) {
                    console.log("获取 TOKEN 失败！");
                }
            });
            return memberList
        },
        getParentDepartmentByChild(departmentId,tk){
            var url = "http://10.1.19.170";
            var memberList = new Array()
            var header={
                "Accept": "application/json; charset=utf-8",
                "Accept-Language": "zh-CN",
                "Content-Type": "application/json; charset=utf-8"
                //"token": tk
            }

            $.ajax({
                url: url + "/seeyon/rest/orgDepartments/parent/"+departmentId+"?token="+tk,
                type: "get",
                dataType: "json",
                async: false,
                headers: header,
                processData: false,
                success: function (data) {
                    memberList = data
                },
                error: function (XMLHttpRequest, textStatus, errorThrown) {
                    console.log("获取 TOKEN 失败！");
                }
            });
            return memberList
        }
    },

}



</script>

<style scoped>
    .content{
        background: #FFFFFF;
    }
    #select_contentTitleSearch{
    	position: fixed;
    	top: 0;
    	line-height: 2.5rem;
    	height: 2.5rem;
    	width: 100%;
    	padding-left: 0.625rem;
    	text-align: left;
    	margin: 0;
    	z-index: 100;
    	background: #FFFFFF;
    }
    .Choose{
    	float: left;
    	width: 7.5rem;
    }
    .select_title{
    	font-size: 1rem;
    	display: inline-block;
    	width: auto;
    	max-width: 80%;
    	vertical-align: middle;
    	text-align: left;
    }
    .Find{
    	float: right;
    	width: 2.1875rem;
    	height: 2.5rem;
    	margin-right: 0.25rem;
    }
    .select_crumbs_list{
    	position: fixed;
    	top: 2.5rem;
    	width: 100%;
    	height: 1.875rem;
    	background: #f4f4f4;
    	z-index: 100;

    }
    .select{
    	width: 95%;
    	height: 1.875rem;
        margin:auto;
    	line-height: 1.875rem;
    	font-size: 1rem;
    	text-overflow: ellipsis;
        display: -webkit-box;
    	white-space: nowrap;
    	overflow: hidden;
    	overflow-x: auto;
    	color: #999;
    }
    .select::-webkit-scrollbar {
        display: none;
    }
    .scroller{
    	margin-top: 5.52rem;
    	margin-bottom: 3.125rem;
    	height: 100%;
    }
    .honebinded{
    	width: 100%;
    	height: 2.5rem;
    	overflow: hidden;
    	padding: 0.3125rem;
    	list-style-type: none;
    	background-color: #fff;
    	border-bottom: none;
    	position: relative;
    	border-bottom: 1px #eee solid;
    }
    .Recent{
    	width: 100%;
    	height: 3.125rem;
    	overflow: hidden;
    	padding: 0.3125rem;
    	list-style-type: none;
    	background-color: #fff;
    	border-bottom: none;
    	position: relative;
    	border-bottom: 1px #eee solid;
    }
    .binded{
    	width: 100%;
    	height: 1.875rem;
    	line-height: 1.875rem;
    	overflow: hidden;
    	padding-left: 0.625rem;
    	color: #999;
    	list-style-type: none;
    	font-size: 1rem;
    	background-color: #f4f4f4;
    }
    .image{
    	max-width: 27%;
    	vertical-align: top;
    	overflow: hidden;
    	display: inline-block;
    	height: 3.125rem;
    	min-width: 12%;
    	padding-top: 0.625rem;
    }
    .image img{
    	width: 1.625rem;
        height: 1.625rem;
        border-radius: 50%;
    	background-color: #0094ff;
    	display: inline-block;
    	color: #fff;
    	font-size: 1rem;
    	text-align: center;
    	line-height: 1.625rem;
    	margin-left: 0.3125rem;
    }
    .titlename{
    	width: 65%;
    	height: 3.125rem;
    	line-height: 3.125rem;
    	overflow: hidden;
    	padding-left: 0.3125rem;
    	display: inline-block;
    	vertical-align: middle;
    	font-size: 1rem;
    	color: #333;
    }
    .selectOrg{
    	width: 18%;
    	color: #3aadfb;
    	text-align: left;
    	height: 2.1875rem;
    	padding-left: 0.5rem;
    	line-height: 2.1875rem;
    	font-size: 1rem;
    	float: right;
    	position: absolute;
    	right: 0;
    	top: 50%;
    	margin-top: -17.5px;
    	border-left:1px #eee solid;
    }
    .avatar{
    	width: 5rem;
    	height: 3.125rem;
    	vertical-align: top;
    	overflow: hidden;
    	display: inline-block;
    	padding-top: 0.375rem;
    	box-sizing: border-box;
    }
    .information{
    	width: 62%;
    	height: 3.125rem;
    	overflow: hidden;
    	padding-top: 0.5rem;
    	display: inline-block;
    	vertical-align: middle;
    }
    input[type='checkbox']{
    	display: inline-block;
    	width: 1.5625rem;
    	height: 1.5625rem;
    	background-color: #fff;
    	-webkit-appearance:none;
    	border: 1px solid #c9c9c9;
    	border-radius: 50%;
    	outline: none;
    }
    .avatar input[type=checkbox]:checked{
        background: url(../../assets/img/gou.png)no-repeat center;
    }
    .avatar img{
    	width: 2.5rem;
    	height: 2.5rem;
    	border-radius: 50%;
    	border: none;
    	cursor: pointer;
    	margin-left: 0.125rem;
    }
    .information span{
    	font-size: 1rem;
    	color: #333333;
    }
    .information h6{
    	line-height: 1rem;
    	width: 100%;
    	color: #999999;
    	padding-top: 0.3125rem;
    	font-size: 0.875rem;
    }
    footer{
    	position:fixed;
    	bottom: 0;
    	z-index: 100;
    	width: 100%;
    	box-sizing: border-box;
    	height: 3.125rem;
    	border-top: 1px solid #dad7d7;
    	background: #FFFFFF;
    }
    footer button{
    	position: absolute;
    	top: 0.3rem;
    	left: 50%;
    	width: 12rem;
    	margin-left: -6rem;
    	text-align: center;
    	line-height: 1.675rem;
    	height: 2.125rem;
    	box-sizing: border-box;
    	padding: 0.25rem 0.75rem 0.25rem 0.75rem;
    	border:1px solid #3aadfb;
    	border-color: #3aadfb;
    	border-radius: 2.125rem;
    	font-size: 1rem;
    	background: #3aadfb;
    	color: #FFFFFF;
    }
    .search{
        position: fixed;
        top: 2.5rem;
        width: 100%;
        height: 2rem;
        background: #eee;
        z-index: 100;
        padding: 0.4375rem 0.5rem;
    }
    .search-input{
        width:82%;
        height:1.875rem;
        border-radius: 0.375rem;
        background: #fff;
        text-align: center;
        font-size: 1rem;
        z-index: 100;
        margin:0 auto;
    }
    .search button{
        background:#eee;
        color:#3aadfb;
        font-size: 1.2rem;
        text-align: center;
        margin-left:0.7rem;
    }
    .search-content{
        z-index: 100;
        position: fixed;
        width: 100%;
        top:5.25rem;
        overflow: hidden;
        background: #eee;
    }

</style>



